import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/Home'
import Admin from '../admin/Admin'

import BlogEdit from '../admin/BlogEdit.vue';
import BlogList from "../admin/BlogList";
import CommentList from "../admin/CommentList";
import FriendList from "../admin/FriendList";
import VisitLog from "../admin/VisitLog";
import VisitorList from "../admin/VisitorList";
import CategoryList from "../admin/CategoryList";
import UserManager from "../admin/UserManager";

import {Message} from 'element-ui';


import store from '@/store'

import {getToken} from '@/request/token'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/admin',
      component: r => require.ensure([], () => r(require('@/admin/admin')), 'mainIndex'),
      meta: {
        requireLogin: false
      },
      children:[
        {
          path: '/writeBlog',
          component: r => require.ensure([], () => r(require('@/admin/BlogEdit')), 'index')
        },
        {
          path: '/blogList',
          component: r => require.ensure([], () => r(require('@/admin/BlogList')), 'index')
        },
        {
          path: '/type',
          component: r => require.ensure([], () => r(require('@/admin/CategoryList')), 'index')
        },
        {
          path: '/type',
          component: r => require.ensure([], () => r(require('@/admin/CategoryList')), 'index')
        },
        {
          path: '/comment',
          component: r => require.ensure([], () => r(require('@/admin/CommentList')), 'index')
        },
        {
          path: '/friendList',
          component: r => require.ensure([], () => r(require('@/admin/FriendList')), 'index')
        },
        {
          path: '/userManager',
          component: r => require.ensure([], () => r(require('@/admin/UserManager')), 'index')
        },
        {
          path: '/visitLog',
          component: r => require.ensure([], () => r(require('@/admin/VisitLog')), 'index')
        },
        {
          path: '/visitor',
          component: r => require.ensure([], () => r(require('@/admin/VisitorList')), 'index')
        },
      ]
    },

    {
      path: '/write/:id?',
      component: r => require.ensure([], () => r(require('@/views/blog/BlogWrite')), 'blogwrite'),
      meta: {
        requireLogin: true
      },
    },
    {
      path: '',
      name: 'Home',
      component: Home,
      children: [
        {
          path: '/',
          component: r => require.ensure([], () => r(require('@/views/Index')), 'index')
        },
        {
          path: '/log',
          component: r => require.ensure([], () => r(require('@/views/Log')), 'log')
        },
        {
          path: '/archives/:year?/:month?',
          component: r => require.ensure([], () => r(require('@/views/blog/BlogArchive')), 'archives')
        },
        {
          path: '/messageBoard',
          component: r => require.ensure([], () => r(require('@/views/MessageBoard')), 'messageboard')
        },
        {
          path: '/view/:id',
          component: r => require.ensure([], () => r(require('@/views/blog/BlogView')), 'blogview')
        },
        {
          path: '/:type/all',
          component: r => require.ensure([], () => r(require('@/views/blog/BlogAllCategoryTag')), 'blogallcategorytag')
        },
        {
          path: '/:type/:id',
          component: r => require.ensure([], () => r(require('@/views/blog/BlogCategoryTag')), 'blogcategorytag')
        }
      ]
    },
    {
      path: '/login',
      component: r => require.ensure([], () => r(require('@/views/Login')), 'login')
    },
    {
      path: '/register',
      component: r => require.ensure([], () => r(require('@/views/Register')), 'register')
    }

  ],
  scrollBehavior(to, from, savedPosition) {
    return {x: 0, y: 0}
  }
})

router.beforeEach((to, from, next) => {

  if (getToken()) {

    if (to.path === '/login') {
      next({path: '/'})
    } else {
      if (store.state.account.length === 0) {
        store.dispatch('getUserInfo').then(data => { //获取用户信息
            next()
        }).catch(() => {
          Message({
            type: 'warning',
            showClose: true,
            message: '登录已过期'
          })
          next({path: '/'})
        })
      } else {

        next()
      }
    }
  } else {
    if (to.matched.some(r => r.meta.requireLogin)) {
      Message({
        type: 'warning',
        showClose: true,
        message: '请先登录哦'
      })

    }
    else {
      next();
    }
  }
})


export default router



